import React from "react";

import { Icon } from "../Icon/Icon";
import { colors } from "../styleguide";

import {
	ErrorText,
	FullWidthRow,
	StyledIconWrapper,
	StyledProgressBar,
	StyledProgressCount,
	StyledWrapper,
} from "./ProgressBar.styles";
import { ProgressBarProps, ProgressBarState } from "./ProgressBar.types";

const MAX = 100;

const getLoadingState = (value: number, isError: boolean, showCompleteState: boolean): ProgressBarState => {
	if (isError) {
		return "error";
	}

	if (value >= 0 && value < MAX) {
		return "default";
	}

	if (showCompleteState && value === MAX) {
		return "complete";
	}

	return "default";
};

const ProgressBar = ({
	ariaLabel,
	value,
	hasError,
	showCompleteState = true,
	hideValue,
	valueBarColor,
	valueBarPattern,
	backgroundColor,
	errorText,
	m,
	mt,
	mb,
	mr,
	ml,
	className,
	dataQA,
	...props
}: ProgressBarProps) => {
	const state = getLoadingState(value, hasError || false, showCompleteState);

	return (
		<StyledWrapper
			data-qa={dataQA}
			m={m}
			mt={mt}
			mr={mr}
			ml={ml}
			mb={mb}
			className={className}
		>
			<FullWidthRow>
				<StyledProgressBar
					{...props}
					tabIndex={0}
					max={MAX}
					value={value}
					aria-valuemax={MAX}
					aria-valuemin={0}
					state={state}
					valueBarColor={valueBarColor}
					valueBarPattern={valueBarPattern}
					backgroundColor={backgroundColor}
					aria-label={ariaLabel}
					role="progressbar">
				</StyledProgressBar>
				{!hideValue &&
					<>
						{state !== "default"
							&& <StyledIconWrapper data-qa="progress-bar-icon-wrapper">
								<Icon
									size={20}
									name={
										state === "error" ? "CircleAlert" : "Check"
									}
									color={
										state === "error" ? colors.destructive : colors.green
									} />
							</StyledIconWrapper>}
						{state === "default"
							&& <StyledProgressCount
								state={state}
								dataQA="progress-bar-progress"
								className="progress-bar-progress"
								variant="bodyCopyBold"
								data-qa={`${dataQA}-progress-bar`}
							>
								{value}%
							</StyledProgressCount>}
					</>
				}
			</FullWidthRow>
			{state === "error" && errorText &&
				<ErrorText
					dataQA="progress-bar-error-text"
					color={colors.destructive}
					variant="caption">
					{errorText}
				</ErrorText>}
		</StyledWrapper>);
};

export default ProgressBar;
